import React, {Component} from 'react';
import {connect} from 'react-redux';
import Payment from './../UserSelection/Payment';

class Header extends Component{
	renderContent() {
		switch(this.props.auth){
			case null:
			return;
			case false:
			return <li><a href="/auth/google">Login With GooGle</a></li>;
			default: 
			return [
			<li key="1"><Payment /></li>,
			<li key="3">Credits {this.props.auth.credits}</li>,
			<li key="2"> <a href="/api/logout">Log Out</a></li>];
		}
	}

	render(){
		console.log("@@@@", this.props);
		var name="";
		if(this.props.auth !== null && this.props.auth !== undefined){
			name = this.props.auth.name;
		}
		return(
			<div>
			<nav>
				  <div className="nav-wrapper">
				    <a href="" className="brand-logo">  {name}</a>
				  
				    <ul id="nav-mobile" className="right hide-on-med-and-down">
				     	{this.renderContent()}
				     	<li><a href="/auth/facebook">Sign in FAcebook</a></li>
				    </ul>

				  </div>
				</nav>
			</div>
		);
	}
}

function mapStateToProps({ auth }) {

return { auth };
}

export default connect(mapStateToProps)(Header);


